export const addProductHandler = (card, stateAddedProducts, stateRegistryInfo, dispatch) => {
    // If its MA Product
    const registryId = parseInt(stateRegistryInfo.id)
    if (card.isMaItem && card.activeVariant) {
        let existingProduct = [...stateAddedProducts].filter(product => product.item.sku === (card.activeVariant?.activeSizeVariant?.size ? card.activeVariant.activeSizeVariant.sku : card.activeVariant.sku))
        if (existingProduct.length === 0) {
            const title = card.title
            const storeItemUrl = card.storeUrl
            const sku = card.activeVariant?.activeSizeVariant?.size ? card.activeVariant.activeSizeVariant.sku : card.activeVariant.sku
            const price = card.activeVariant?.activeSizeVariant?.size ? parseFloat(card.activeVariant.activeSizeVariant.price) : parseFloat(card.activeVariant.price)
            const compareAtPrice = card.activeVariant?.activeSizeVariant?.size ? parseFloat(card.activeVariant.activeSizeVariant.compareAtPrice) : parseFloat(card.activeVariant.compareAtPrice)
            const quantity = parseInt(card.quantity)
            const imageUrl = (card.activeVariant?.activeSizeVariant?.size && card.activeVariant.activeSizeVariant.imageUrl) ? card.activeVariant.activeSizeVariant.imageUrl : (card.activeVariant.imageUrl && card.activeVariant.imageUrl !== "") ? card.activeVariant.imageUrl : card.imageUrl;
            const shopifyVariantId = card.activeVariant?.activeSizeVariant?.size ? card.activeVariant.activeSizeVariant.variantId : card.activeVariant.variantId
            const storeName = card.storeName
            const mostWanted = card.mostWanted ? card.mostWanted : false
            const isGroupGift = card.isGroupGift ? card.isGroupGift : false
            const isCashAcceptedOnly = false
            const shopifyItemId = card.shopifyItemId
            const isBundleProduct = card.isBundleProduct ? card.isBundleProduct : false
            let category = null
            let bundleProduct = [...card.itemRecommendationCategoryItemRecommendation].map(cat => {
                return {
                    categoryId: cat.category.id,
                    itemsCount: cat.itemsCount
                }
            })
            if (card.isBundleProduct) {
                category = [...card.itemRecommendationCategoryItemRecommendation].map(cat => Number(cat.id));
            } else if (!category && card.activeVariant?.activeSizeVariant?.size && card.activeVariant?.activeSizeVariant.registryCategoryId.length) {
                category = card.activeVariant?.activeSizeVariant.registryCategoryId.map(cat => Number(cat.id))
            } else {
                category = [Number(card.category.id)]
            }

            let data = {
                query: ADD_REGISTRY_ITEM,
                variables: {
                    registryId: registryId,
                    title: title,
                    // TODO: change category
                    category: category,
                    tags: [],
                    storeItemUrl: storeItemUrl ? storeItemUrl : '',
                    sku: sku,
                    price: price,
                    quantity: quantity,
                    imageUrl: imageUrl,
                    shopifyItemVariantId: shopifyVariantId,
                    storeName: storeName,
                    mostWanted: mostWanted,
                    isGroupGift: isGroupGift,
                    //TODO: change this if requried
                    // shopifyItemId: shopifyItemId,
                    shopifyItemId: shopifyItemId,
                    isCashAcceptedOnly: isCashAcceptedOnly,
                    isBundleProduct: isBundleProduct,
                    bundleProduct: bundleProduct,
                    note: card.giftNote,
                    compareAtPrice: compareAtPrice,
                }
            }
            addProductToRegistry(data, registryId, card.title, dispatch)
        } else {
            let data = {
                query: UPDATE_REGISTRY_ITEM,
                variables: {
                    registryItemId: existingProduct[0].id,
                    quantity: parseInt(card.quantity) + parseInt(existingProduct[0].quantity),
                    mostWanted: card.mostWanted ? card.mostWanted : existingProduct[0].mostWanted,
                    isGroupGift: card.isGroupGift ? card.isGroupGift : existingProduct[0].isGroupGift,
                    isCashAcceptedOnly: card.isCashAcceptedOnly ? card.isCashAcceptedOnly : existingProduct[0].isCashAcceptedOnly,
                    title: existingProduct[0].title,
                    storeItemUrl: existingProduct[0].storeItemUrl,
                    price: existingProduct[0].price,
                    category: [...existingProduct[0].registryCategories].map(cat => Number(cat.id)),
                    note: card.giftNote ? card.giftNote : existingProduct[0].note,
                }
            }
            updateProductToRegistry(data, registryId, card.title, dispatch)
        }
    }
    // If its not MA Product
    else {
        let existingProduct = [...stateAddedProducts].filter(product => product.item.sku === card.sku)
        if (existingProduct.length === 0) {
            const title = card.title
            const storeItemUrl = card.storeUrl
            const sku = card.sku
            const price = parseFloat(card.price)
            const quantity = parseInt(card.quantity)
            const imageUrl = card.imageUrl
            const storeName = card.storeName
            const mostWanted = card.mostWanted ? card.mostWanted : false
            const isCashAcceptedOnly = card.isCashAcceptedOnly === false ? card.isCashAcceptedOnly : true
            const isGroupGift = card.isGroupGift ? card.isGroupGift : false
            const isBundleProduct = card.isBundleProduct ? card.isBundleProduct : false
            const category = card.isBundleProduct ? [...card.itemRecommendationCategoryItemRecommendation].map(cat => Number(cat.id)) : card.hasMultiCategories ? card.categories : [Number(card.category.id)]
            let bundleProduct = [...card.itemRecommendationCategoryItemRecommendation].map(cat => {
                return {
                    categoryId: cat.category.id,
                    itemsCount: cat.itemsCount
                }
            })

            let data = {
                query: ADD_REGISTRY_ITEM,
                variables: {
                    registryId: registryId,
                    title: title,
                    // TODO: change category
                    category: category,
                    tags: [],
                    storeItemUrl: storeItemUrl ? storeItemUrl : '',
                    sku: sku,
                    price: price,
                    quantity: quantity,
                    imageUrl: imageUrl,
                    storeName: storeName,
                    mostWanted: mostWanted,
                    isGroupGift: isGroupGift,
                    isCashAcceptedOnly: isCashAcceptedOnly,
                    isBundleProduct: isBundleProduct,
                    bundleProduct: bundleProduct,
                    note: card.giftNote,
                }
            }
            addProductToRegistry(data, registryId, card.title, dispatch)
        } else {
            let data = {
                query: UPDATE_REGISTRY_ITEM,
                variables: {
                    registryItemId: existingProduct[0].id,
                    title: card.title ? card.title : existingProduct[0].title,
                    price: card.price ? card.price : existingProduct[0].price,
                    storeItemUrl: card.storeItemUrl ? card.storeItemUrl : existingProduct[0].storeItemUrl,
                    category: card.hasMultiCategories ? card.categories : card.isBundleProduct ? [...card.itemRecommendationCategoryItemRecommendation].map(cat => Number(cat.id)) : [Number(card.category.id)],
                    quantity: parseInt(card.quantity) + parseInt(existingProduct[0].quantity),
                    mostWanted: card.mostWanted ? card.mostWanted : existingProduct[0].mostWanted,
                    isGroupGift: card.isGroupGift ? card.isGroupGift : existingProduct[0].isGroupGift,
                    isCashAcceptedOnly: card.isCashAcceptedOnly ? card.isCashAcceptedOnly : existingProduct[0].isCashAcceptedOnly,
                    note: card.giftNote ? card.giftNote : existingProduct[0].note,
                }
            }
            updateProductToRegistry(data, registryId, card.title, dispatch)
        }
    }
}

export const getOnboardingQuestions = async (dispatch, registryId = null, token = null) => {
    let reqData = {
        query: GET_QUESTIONS,
        variables: {
            isPersonalize: false,
            registryId: registryId ? Number(registryId) : null
        }
    }
    let options = {}
    if (token) {
        options = {headers: {"Authorization": "Token " + token}}
    }

    let getAnswerResponse = (question, isFollowUp = false) => {
        let response = {
            question: question.id,
            response: {
                option: question.answers[0].questionOption ? question.answers[0].questionOption.id : 0,
                optionText: question.answers[0].optionText,
                action: null
            }
        }
        if (isFollowUp){
            response.response['isFollowUp'] = true
            delete response.response.action;
        }
        return response;
    }

    await graphQLNoHeader(reqData, options).then(async res => {
        if (res.data.data.questions) {
            let alreadyAnswered = []
            const questions = res.data.data.questions.map(question => {
                let followUpAnswers = []
                let currentQuestionOptionAnswerId = null
                question.isAnswered = question.answers.length > 0;

                if (question.isAnswered) {
                    const currentQuestionAnswer = getAnswerResponse(question)
                    currentQuestionOptionAnswerId = currentQuestionAnswer.response.option
                    alreadyAnswered.push(currentQuestionAnswer)
                }
                question.options = question.options.map(option => {
                    if (option.followUpQuestions.length > 0) {
                        option.followUpQuestions.map(followUpQuestion => {
                            followUpQuestion['parentQuestion'] = question.id
                            followUpQuestion.question['isAnswered'] = followUpQuestion.question.answers.length > 0

                            if (followUpQuestion.question['isAnswered'] && option.id === currentQuestionOptionAnswerId){
                                followUpAnswers.push(getAnswerResponse(followUpQuestion.question, true))
                            }

                            return followUpQuestion;
                        })
                    }
                    return option;
                })


                if (followUpAnswers.length > 0){
                    alreadyAnswered.push(...followUpAnswers)
                }
                return question;

            })
            dispatch(updateQuestionAnswer(alreadyAnswered))
            await dispatch(addOnboardingQuestion(questions))
        }
    }).catch(err => {
        // toast.error("Error occurred while fetching questions");
        console.error(err);
    })
}


export const getPersonalizeQuestions = (registryId, dispatch) => {
    let reqData = {
        query: GET_PERSONALIZE_QUESTIONS,
        variables: {
            registryId: registryId,
        }
    }

    graphQL(reqData).then(res => {
        if (res.data.data.editablePersonalizeQuestions) {
            const questions = res.data.data.editablePersonalizeQuestions.map(question => {
                question.options = question.options.map(option => {
                    if (option.followUpQuestions.length > 0) {
                        option.followUpQuestions.map(followUpQuestion => {
                            followUpQuestion['parentQuestion'] = question.id
                            return followUpQuestion;
                        })
                    }
                    return option;
                })
                question.isAnswered = false;
                question.toastMessage = question.toastMessage.replace("\n", '<br>')
                return question;
            })
            dispatch(addPersonalizeQuestion(questions))
        }
    }).catch(err => {
        toast.error("Error occurred while fetching questions");
        console.error(err);
    })
}

export const updateGuestViewRegistry = (urlSlug, dispatch) => {
    let reqData = {
        query: GET_GUEST_REGISTRY,
        variables: {
            urlSlug: urlSlug,
        }
    }

    graphQL(reqData).then(res => {
        if (res.data.data.registryAsGuest) {
            let guestViewInfo = {...res.data.data.registryAsGuest}
            if (guestViewInfo.image) {
                guestViewInfo.image.image = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + guestViewInfo.image.image;
            }
            delete guestViewInfo.registryItems
            dispatch(updateGuestViewInfo(guestViewInfo))
        }
    }).catch(err => {
        dispatch(updateGuestViewInfo({notFound: true}))
        if (err.response && err.response.data.errors) {
            for (const error of err.response.data.errors) {
                // TODO: Implement toast if necessary
                console.log(error.message)
            }
        }
    })
}

