export const formatPrice = (price, currency = CURRENCY) => {
    if (price) {
        try {
            if (String(price).split(".").length > 1) {
                price = Number(price).toFixed(2);
            }
            price = String(price).replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            return `${currency}${price}`
        } catch {

        }
    }
    return price
}

