export const viewRegistryTourPosition = (node, top = 0, left = 0) => {
    if (document.body.offsetWidth <= 786) {
        let tourEl = document.querySelector('.reactour__helper')
        let markPositionY = node.getBoundingClientRect().y
        let markPositionLeft = node.getBoundingClientRect().left
        let tourHeigt = null
        if (tourEl) {
            tourHeigt = tourEl.offsetHeight
            tourEl.style.top = ((top !== false) ? (markPositionY - top) : (markPositionY * 2 - 40)) + 'px'
            tourEl.style.left = (left !== false) ? (markPositionLeft - left) + 'px' : 'unset';
            tourEl.style.right = (left === false) ? '12px' : 'unset';
            tourEl.style.transform = `translate(0px,-100%)`;
        }
    }
}

